"use client"

import { useState, useEffect, useRef } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { ArrowLeft, Send, Sparkles, User, Loader2, LogOut } from "lucide-react"

// WebSocket 라이브러리
import { Client } from "@stomp/stompjs"
import SockJS from "sockjs-client"

export default function AIChatPage() {
  const router = useRouter()
  const [currentUserId, setCurrentUserId] = useState<string | null>(null)
  const [userName, setUserName] = useState("")
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const [isConnected, setIsConnected] = useState(false)
  const [currentRoomId, setCurrentRoomId] = useState<string | null>(null)
  
  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)

  const API_BASE = "http://sparta-nextme.xyz:18080"

  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    const userId = localStorage.getItem("userId")
    const name = localStorage.getItem("name") || "사용자"
    
    setCurrentUserId(userId)
    setUserName(name)

    if (!token) {
      router.push("/login")
      return
    }

    const client = new Client({
      brokerURL: `ws://sparta-nextme.xyz:18080/ws/chat`,
      connectHeaders: { Authorization: `Bearer ${token}` },
      debug: () => {},
      reconnectDelay: 5000,
      webSocketFactory: () => new SockJS(`${API_BASE}/ws/chat`),
    })

    client.onConnect = () => {
      setIsConnected(true)
      stompClient.current = client
      initChatRoom(token, userId)
    }

    client.onStompError = () => setIsConnected(false)
    client.activate()
    stompClient.current = client

    return () => {
      if (stompClient.current) stompClient.current.deactivate()
    }
  }, [])

  const initChatRoom = async (token: string, userId: string | null) => {
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=AI`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const aiRooms = data.rooms || data
      let roomId = aiRooms?.[0]?.chatRoomId || aiRooms?.[0]?.id

      if (!roomId) {
        const createRes = await fetch(`${API_BASE}/v1/chats/room`, {
          method: "POST",
          headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
          body: JSON.stringify({ roomType: "AI", userId })
        })
        const newData = await createRes.json()
        roomId = newData.chatRoomId
      }

      if (roomId) {
        setCurrentRoomId(roomId)
        const msgRes = await fetch(`${API_BASE}/v1/chats/message/${roomId}?size=20`, {
          headers: { "Authorization": `Bearer ${token}` }
        })
        const msgData = await msgRes.json()
        const fetchedMsgs = Array.isArray(msgData) ? msgData : (msgData.messages || [])
        
        if (fetchedMsgs.length === 0) {
          setMessages([{
            senderId: "AI",
            content: "안녕하세요! Next Me AI 어시스턴트입니다. 재무 관련 궁금한 점을 물어보세요.",
            createdAt: new Date().toISOString()
          }])
        } else {
          setMessages(fetchedMsgs.reverse())
        }
        
        subscribeToRoom(roomId)
        scrollToBottom()
      }
    } catch (e) {
      console.error(e)
    }
  }

  const subscribeToRoom = (roomId: string) => {
    if (stompClient.current?.connected) {
      stompClient.current.subscribe(`/topic/chat.room.${roomId}`, (message) => {
        const newMessage = JSON.parse(message.body)
        setMessages((prev) => [...prev, newMessage])
        setIsTyping(false)
        scrollToBottom()
      })
    }
  }

  const handleSend = (manualText?: string) => {
    const textToSend = manualText || inputValue
    if (!textToSend.trim() || !stompClient.current?.connected || !currentRoomId) return

    stompClient.current.publish({
      destination: `/app/chat.send/${currentRoomId}`,
      body: JSON.stringify({ content: textToSend, roomType: "AI" }),
    })

    setInputValue("")
    setIsTyping(true)
    scrollToBottom()
  }

  // ★ 방 나가기 기능: 소켓 연결을 끊고 대시보드로 이동
  const handleLeaveRoom = () => {
    if (confirm("대화를 종료하고 채팅방을 나가시겠습니까?")) {
      if (stompClient.current) {
        stompClient.current.deactivate()
      }
      router.push("/dashboard") // 대시보드나 이전 페이지로 이동
    }
  }

  const scrollToBottom = () => {
    setTimeout(() => {
      if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }, 100)
  }

  return (
    <div className="min-h-screen flex flex-col bg-background">
      {/* Header */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between max-w-4xl">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" onClick={() => router.back()}>
              <ArrowLeft className="h-5 w-5" />
            </Button>
            <div className="flex items-center gap-2">
              <div className="h-8 w-8 rounded-full bg-primary flex items-center justify-center">
                <Sparkles className="h-4 w-4 text-primary-foreground" />
              </div>
              <div>
                <p className="font-semibold text-sm">AI 어시스턴트</p>
                <p className={`text-[10px] ${isConnected ? 'text-green-500' : 'text-muted-foreground'}`}>
                  {isConnected ? '● 온라인' : '○ 연결 중...'}
                </p>
              </div>
            </div>
          </div>
          
          <div className="flex items-center gap-2">
            <Link href="/">
              <Image src="/logo.png" alt="Next Me" width={100} height={27} className="h-7 w-auto" />
            </Link>
            {/* 방 나가기 버튼 추가 */}
            <Button variant="ghost" size="icon" onClick={handleLeaveRoom} title="방 나가기" className="text-muted-foreground hover:text-destructive">
              <LogOut className="h-5 w-5" />
            </Button>
          </div>
        </div>
      </header>

      {/* Chat Messages */}
      <div ref={scrollRef} className="flex-1 overflow-y-auto">
        <div className="container mx-auto px-4 py-8 max-w-4xl">
          <div className="space-y-6">
            {messages.map((msg, idx) => {
              const isMine = msg.senderId === currentUserId
              return (
                <div key={idx} className={`flex gap-4 ${isMine ? "justify-end" : "justify-start"}`}>
                  {!isMine && (
                    <div className="h-10 w-10 rounded-full bg-primary flex items-center justify-center flex-shrink-0 shadow-sm">
                      <Sparkles className="h-5 w-5 text-primary-foreground" />
                    </div>
                  )}
                  <Card className={`p-4 max-w-[75%] shadow-sm ${isMine ? "bg-primary text-primary-foreground border-none" : "bg-card"}`}>
                    <p className="text-sm leading-relaxed">{msg.content}</p>
                  </Card>
                  {isMine && (
                    <Avatar className="flex-shrink-0 border">
                      <AvatarFallback className="text-xs bg-muted font-bold">{userName.charAt(0)}</AvatarFallback>
                    </Avatar>
                  )}
                </div>
              )
            })}
            {isTyping && (
               <div className="flex gap-4 justify-start animate-pulse">
                 <div className="h-10 w-10 rounded-full bg-muted flex items-center justify-center"><Loader2 className="h-4 w-4 animate-spin" /></div>
                 <Card className="p-4 bg-muted border-none"><p className="text-xs">생각 중...</p></Card>
               </div>
            )}
          </div>

          {/* Suggested Questions */}
          {messages.length === 1 && !isTyping && (
            <div className="mt-12 space-y-4 animate-in fade-in slide-in-from-bottom-4 duration-700">
              <p className="text-sm text-muted-foreground text-center font-medium">자주 묻는 질문</p>
              <div className="grid md:grid-cols-2 gap-3">
                {[
                  "주택 구매를 위해 얼마나 저축해야 하나요?",
                  "은퇴 준비는 언제부터 시작하는 게 좋을까요?",
                  "지출을 줄일 수 있는 방법이 있나요?",
                  "투자는 어떻게 시작하면 좋을까요?"
                ].map((q, i) => (
                  <Button
                    key={i}
                    variant="outline"
                    className="h-auto p-4 text-left justify-start bg-transparent hover:bg-primary/5 hover:border-primary/30 transition-all"
                    onClick={() => handleSend(q)}
                  >
                    <span className="text-sm break-keep">{q}</span>
                  </Button>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Input Area */}
      <div className="border-t border-border bg-card/50 backdrop-blur-sm sticky bottom-0">
        <div className="container mx-auto px-4 py-4 max-w-4xl">
          <form
            onSubmit={(e) => {
              e.preventDefault()
              handleSend()
            }}
            className="flex gap-2"
          >
            <Input
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              placeholder={isConnected ? "메시지를 입력하세요..." : "연결 대기 중..."}
              className="flex-1 bg-background h-12 rounded-xl"
              disabled={!isConnected}
            />
            <Button type="submit" size="icon" className="h-12 w-12 rounded-xl" disabled={!isConnected || !inputValue.trim()}>
              <Send className="h-4 w-4" />
            </Button>
          </form>
          <p className="text-[10px] text-center text-muted-foreground mt-2">
            AI의 답변은 참고용이며 금융 결정을 위한 최종 판단은 본인에게 있습니다.
          </p>
        </div>
      </div>
    </div>
  )
}